*{box-sizing:border-box}body{margin:0;font-size:16px;font-family:"Overpass Mono",sans-serif;line-height:1.5;background-color:#080808;color:#faffaf}main{width:100%;position:relative;display:flex;flex-wrap:wrap}main .section_1-2{width:100%;padding:1.6rem}@media (min-width: 1000px){main .section_1-2{width:50%}}main .section-fixed{height:100vh;overflow:visible;overflow-y:scroll}main .page{display:flex;justify-content:center;align-items:center;margin-left:3.2rem}main .page .content{width:85%;padding:1.6rem}@media (max-width: 1000px){main .page .content{margin-left:3rem;width:100%}}@media (max-width: 1000px){main .header-layout{margin-left:6.4rem}}.bg_color{background-color:#080808}.main_color{background-color:#faffaf}a:link{text-decoration:none}p,ul{font-size:.95rem;font-weight:100}.b{font-weight:700}.small{font-size:.75rem}.title{font-family:"Anton",sans-serif;text-transform:uppercase;font-weight:700;line-height:1.15;margin:1.6rem 0}.t1{font-size:6rem}.t2{font-size:4.8rem}.t3{font-size:2.4rem;line-height:1.15;letter-spacing:0.065em}.small{font-size:.875rem;margin-top:.2rem}.btn{background-color:#faffaf;border:2px solid #faffaf;color:#080808;cursor:pointer;font-family:"Overpass Mono",sans-serif;font-size:.875rem;padding:.8rem;text-transform:uppercase;text-decoration:none;transition:all ease-in-out .3s}.btn:hover{background-color:#f9ffa0;color:#000}.btn-line{border:1px solid rgba(250,255,175,0.6);border-radius:0;background-color:transparent;color:#faffaf;cursor:pointer;font-family:"Overpass Mono",sans-serif;font-size:.875rem;font-weight:500;padding:.725rem;text-transform:uppercase;text-decoration:none;transition:all ease-in-out .3s}.btn-line:hover{color:#f9ffa0}.btn-black{background-color:#080808;border:2px solid #faffaf;color:#faffaf;cursor:pointer;font-family:"Overpass Mono",sans-serif;font-size:.875rem;padding:.8rem;text-transform:uppercase;text-decoration:none;transition:all ease-in-out .3s}.btn-black:hover{background-color:#000;color:#f9ffa0}.btn-line-black{border:1px solid rgba(8,8,8,0.6);border-radius:0;background-color:transparent;color:#080808;cursor:pointer;font-family:"Overpass Mono",sans-serif;font-size:.875rem;font-weight:500;padding:.725rem;text-transform:uppercase;text-decoration:none;transition:all ease-in-out .3s}.btn-line-black:hover{color:#000}.vertical-text{-webkit-writing-mode:tb-rl;-ms-writing-mode:tb-rl;writing-mode:tb-rl;transform:rotate(-180deg)}.horizontal-list{padding:0;margin:0;display:flex;list-style:none;align-items:center}.horizontal-list li{margin-right:.8rem}.horizontal-list li:last-child{margin-right:0}::-webkit-input-placeholder{font-size:.875rem;color:rgba(8,8,8,0.6);text-transform:uppercase}:-moz-placeholder{font-size:.875rem;color:rgba(8,8,8,0.6);text-transform:uppercase}::-moz-placeholder{font-size:.875rem;color:rgba(8,8,8,0.6);text-transform:uppercase}:-ms-input-placeholder{font-size:.875rem;color:rgba(8,8,8,0.6);text-transform:uppercase}

.book-list-container{display:flex;justify-content:center}.book-list-container .book-list{display:flex;flex-wrap:wrap;list-style:none;align-items:center;margin:0;padding:0}.book-list-container .book-list li{height:94vh;display:flex;align-items:flex-end}.book-list-container .book-list li a{display:flex;align-items:flex-end;height:100%;margin:3.2rem 0 1.6rem}.book-list-container .book-list li a div{background-color:#faffaf;color:#080808;border:1px solid #080808;text-transform:uppercase;position:relative;display:flex;align-items:center;justify-content:space-between;font-family:"Anton",sans-serif;padding:.8rem 0;transition:background-color ease-in .1s;cursor:pointer}.book-list-container .book-list li a div:hover{background-color:#fdffe2}.book-list-container .book-list li a div span{letter-spacing:0.015em}.book-list-container .book-list li.picked-book div{background-color:#080808}.book-list-container .book-list li.picked-book div:hover{background-color:#080808}.book-list-container .book-list.search-mode li div{background-color:rgba(250,255,175,0.2)}.book-list-container .book-list.search-mode li.selected-by-tag div{background-color:#faffaf}

.add-book form{color:#080808;background-color:#faffaf}.add-book form .field{padding:0 .4rem;margin-bottom:1.6rem}.add-book form .field label{display:block;margin-bottom:.2rem}.add-book form .field input,.add-book form .field textarea{display:block;background-color:transparent;width:100%;box-shadow:none;border:1px dotted rgba(8,8,8,0.6);padding:.2rem .4rem;font-size:1.235rem;font-family:"Overpass Mono",sans-serif;font-weight:600;letter-spacing:-0.05em}.add-book form fieldset{border:none;padding:0}.add-book form fieldset input[type="checkbox"]{display:none}.add-book form fieldset .tag{display:inline-flex;border:1px solid rgba(8,8,8,0.6)}.add-book form fieldset .tag:hover{background-color:rgba(255,255,255,0.3);box-shadow:0 0 6px rgba(8,8,8,0.3)}.add-book form fieldset .tag.is-checked{background-color:rgba(255,255,255,0.3);box-shadow:0 0 6px rgba(8,8,8,0.3);color:#080808;font-weight:700}.add-book form .buttons{padding:0 .4rem;display:flex;justify-content:flex-end;align-items:center}.add-book form .buttons>*:first-child{margin-right:.4rem}

fieldset,.tags-group{border:none;padding:0}fieldset input[type="checkbox"],.tags-group input[type="checkbox"]{display:none}fieldset a:visited,fieldset a:link,.tags-group a:visited,.tags-group a:link{color:#faffaf}fieldset .tag,.tags-group .tag{border:1px solid rgba(250,255,175,0.6);border-radius:.2rem;padding:.2rem .4rem;display:inline-flex;margin:.4rem .4rem 0 0;text-transform:uppercase;font-size:.75rem;font-weight:500;cursor:pointer;transition:all ease-in .2s}fieldset .tag:hover,.tags-group .tag:hover{background-color:rgba(255,255,255,0.3);box-shadow:0 0 6px rgba(8,8,8,0.3)}fieldset .tag.is-checked,.tags-group .tag.is-checked{background-color:rgba(250,255,175,0.9);border:1px solid rgba(8,8,8,0.4);color:#080808;font-weight:700}

.intro{padding:0 3.2rem}.intro .title{max-width:32rem;margin-bottom:3.2rem}.intro .content{margin-bottom:3.2rem;max-width:36rem}.intro .content p{margin-top:0}.intro .cta .btn{margin-bottom:.8rem}@media (min-width: 600px){.intro .cta .btn{margin-right:1.6rem}}

header{position:fixed;top:0;left:0;bottom:0;border-right:1px solid #faffaf;padding:3.2rem 1.6rem;display:flex;flex-direction:column;align-items:center;justify-content:space-between;height:100vh}header .title{color:#faffaf;-webkit-text-fill-color:#080808;-webkit-text-stroke-width:0.015rem;-webkit-text-stroke-color:#faffaf}

.thank-you .content{border:1px solid #faffaf;background-color:#080808;color:#faffaf}.thank-you .go-back{border-top:1px dotted #faffaf;margin:1.6rem -1.6rem 0;padding:1.6rem 1.6rem 0}

